:root {
  --main-bg-color: #fff;
  --primary-color: #3369f3;
  --attention-color: red;
  --second-color: #fff;
  --main-text-color: #1e1e1e;
  --secondary-text-color: #999;
  --border-color: #eaeaea;
}

.back {
  height: 100%;
  width: 64px;
  border: 1px var(--border-color);
  border-right-style: solid;
  display: flex;
}

.back_icon {
  height: 28px;
  width: 28px;
  margin: auto;
}

.profile_content {
  flex: 1;
  display: flex;
}

.content_card {
  width: 510px;
  flex-flow: column;
  margin: auto;
  display: flex;
}

.card_title {
  text-align: center;
  color: var(--main-text-color);
  margin-top: 21px;
  margin-bottom: 60px;
  font-family: Inter, sans-serif;
  font-size: 16px;
  font-style: normal;
  font-weight: 600;
}

.card_field {
  border: 1px var(--border-color);
  height: 27px;
  border-bottom-style: solid;
  flex-flow: wrap;
  place-content: center space-between;
  margin-top: 10px;
  display: flex;
}

.card_field_name {
  text-align: left;
  color: var(--main-text-color);
  font-family: Inter, sans-serif;
  font-size: 13px;
  font-style: normal;
  font-weight: 500;
}

.card_field_value {
  text-align: right;
  color: var(--secondary-text-color);
  border: 0;
  font-family: Inter, sans-serif;
  font-size: 13px;
  font-style: normal;
  font-weight: 500;
}

.avatar {
  width: 130px;
  height: 130px;
  margin: auto;
}

.card_footer {
  flex-flow: column;
  margin-top: 60px;
  display: flex;
}

.footer_button {
  width: 100%;
  border: 1px var(--border-color);
  cursor: pointer;
  text-align: left;
  border: none;
  font-family: Inter, sans-serif;
  font-style: normal;
  font-weight: 500;
}

.button__active {
  background: var(--main-bg-color);
  color: var(--primary-color);
  height: 37px;
  font-size: 13px;
  line-height: 37px;
}

.button__red {
  background: var(--main-bg-color);
  color: var(--attention-color);
  font-size: 13px;
  line-height: 37px;
}

